:root {
  --bg: #ffffff;
  --text: #1f2937;
  --muted: #6b7280;
  --brand: #111827;
  --accent: #2563eb;
  --surface: #f8fafc;
  --border: #e5e7eb;
  --radius: 12px;
}

* { box-sizing: border-box; }
html, body { margin: 0; padding: 0; background: var(--bg); color: var(--text); font: 16px/1.65 -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, "PingFang SC", "Microsoft Yahei", sans-serif; }
img, video, iframe { max-width: 100%; display: block; }
a { color: inherit; }

.container { width: min(1100px, 92%); margin: 0 auto; }
.narrow { width: min(850px, 92%); margin: 0 auto; }

/* Header：半透明粘顶 */
.site-header { position: sticky; top: 0; z-index: 10; backdrop-filter: saturate(1.5) blur(8px); background: rgba(255,255,255,.85); border-bottom: 1px solid var(--border); }
.header-inner { display: flex; align-items: center; justify-content: space-between; height: 56px; }
.brand { font-weight: 700; color: var(--brand); }
.nav a { margin-left: 16px; text-decoration: none; color: var(--text); }
.nav a:hover { color: var(--accent); }

/* Sections */
.section { padding: 56px 0; }
.section h2 { margin: 0 0 16px; font-size: 1.6rem; }
.hint { color: var(--muted); font-size: .95rem; }

/* Hero */
.hero { padding-top: 72px; }
.hero h1 { font-size: clamp(1.8rem, 3vw, 2.6rem); margin: 0 0 8px; }
.authors, .affiliations { color: var(--muted); margin: 6px 0; }

/* Buttons */
.cta { margin: 16px 0 20px; display: flex; flex-wrap: wrap; gap: 10px; }
.btn { display: inline-block; padding: 10px 14px; border: 1px solid var(--border); border-radius: 8px; text-decoration: none; color: var(--text); background: var(--surface); }
.btn.primary { background: var(--accent); color: #fff; border-color: var(--accent); }
.btn:hover { filter: brightness(.97); }

/* Cards */
.card { border: 1px solid var(--border); border-radius: var(--radius); overflow: hidden; background: #fff; }

/* Media */
.teaser { margin-top: 12px; }
.media {}
.pdf-frame { aspect-ratio: 16 / 11; border: 1px solid var(--border); border-radius: var(--radius); overflow: hidden; }
.pdf-frame iframe { width: 100%; height: 100%; border: 0; }

/* Grid */
.grid { display: grid; grid-template-columns: repeat(auto-fill, minmax(220px, 1fr)); gap: 12px; }
.grid-item.card img { width: 100%; height: auto; display: block; }

/* Code block */
pre.code { background: #0b1020; color: #e6edf3; padding: 16px; border-radius: var(--radius); overflow: auto; font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace; }

/* Footer */
.site-footer { border-top: 1px solid var(--border); background: var(--surface); }
.footer-inner { display: flex; align-items: center; justify-content: center; gap: 10px; height: 64px; color: var(--muted); }
.sep { opacity: .6; }

/* Lightbox */
.lightbox { position: fixed; inset: 0; background: rgba(0,0,0,.75); display: none; align-items: center; justify-content: center; padding: 24px; }
.lightbox img { max-width: min(1000px, 94vw); max-height: 90vh; border-radius: 8px; }
.lightbox.show { display: flex; }

/* Mobile */
@media (max-width: 480px) {
  .nav { display: none; }
  .pdf-frame { aspect-ratio: 3 / 4; }
}
